@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body {
  padding: 60px;
  font-family: "Press Start 2P", cursive;
}

form {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.nes-input {
  width: 85%;
  margin-right: 20px;
}

input {
  width: 80%;
  font-size: 0.9 rem;
  color: rgb(153, 153, 153);
}

.nes-container {
  width: 100%;
  margin-top: 20px;
}

.image {
  width: 80px;
}

ul {
  padding: 0;
}

.card-grid {
  margin: 20px;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-gap: 20px;
}

.card {
  align-content: center;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
  height: auto;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
  width: 100%;
}

